import React, { useContext } from 'react';
import icon from '../../../constants/icon';
import DetailCommentItem from './DetailCommentItem'
import SectionTitle from '../../SectionTitle';
import ButtonCus from '../../../components/ButtonCus/index';
import { AppContext } from '../../../context/AppProvider';


const cmtList = [
      { id: 1, name: 'Qu Trm', rateStar: 4, date: '18-10-2021', text: 'Khng gian sang trng, t vn cng ok, dch v cng kh n nhng hi t.nh gi 3 sao v nhn vin mn t ni l check in g y nhng tht ra l t nh gi 5 sao' },
      { id: 2, name: 'Tam Nguyen', rateStar: 5, date: '20-11-2021', text: 'Cht lng phc v tt . Nhn vin rt thn thin v nhit tnh t vn theo nhu cu khc hng' },
      { id: 3, name: 'Vuong Xuan Duy', rateStar: 4, date: '21-11-2021', text: 'Nhn vin t vn nhit tnh , ch  chm sc hu mi lu di. S quay li s dng dch v ln sau. ' },
      { id: 4, name: 'Qu Trm', rateStar: 4, date: '18-10-2021', text: 'Khng gian sang trng, t vn cng ok, dch v cng kh n nhng hi t.nh gi 3 sao v nhn vin mn t ni l check in g y nhng tht ra l t nh gi 5 sao' },
      { id: 5, name: 'Tam Nguyen', rateStar: 5, date: '20-11-2021', text: 'Cht lng phc v tt . Nhn vin rt thn thin v nhit tnh t vn theo nhu cu khc hng' },
      { id: 6, name: 'Vuong Xuan Duy', rateStar: 4, date: '21-11-2021', text: 'Nhn vin t vn nhit tnh , ch  chm sc hu mi lu di. S quay li s dng dch v ln sau. ' },
]
function DetailComment(props: any) {
      const { styleCmt } = props;
      const { t } = useContext(AppContext)
      return (
            <div
                  style={{ width: styleCmt?.width }}
                  className="mer-detail__content-cmt"
            >
                  <div className="flex-row-sp mer-detail-cmt__head">
                        <span className="flex-row-sp">
                              <SectionTitle
                                    title={t('Mer_de.feedback')}
                              />
                              <h1 className="flex-row mer-detail-cmt__head-star">
                                    4.5/5
                                    <img src={icon.star} alt="" />
                              </h1>
                        </span>
                        <div className="mer-detail-cmt__head-total">
                              <span>150</span> {t('Mer_de.feedbacks')}
                        </div>
                  </div>
                  <ul className="mer-detail-cmt__box">
                        {
                              cmtList.map(item => (
                                    <DetailCommentItem
                                          key={item.id}
                                          comment={item}
                                    />
                              ))
                        }
                  </ul>
                  <ButtonCus
                        text={t('Mer_de.view_all_feedback')}
                        imgIcon={icon.next}
                        color="var(--purple)"
                        border="solid 1px var(--purple)"
                        borderRadius="18px"
                  />
            </div>
      );
}

export default DetailComment;